import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import '../styles/RegisterPage.css'; // ,   CSS  

const RegisterPage = () => {
    const [formData, setFormData] = useState({
        email: '',
        username: '',
        password: '',
        city: '',
        region: '',
        role: 'user',
        specialtyId: null,
        clinicId: null,
        phoneNumber: '',
        age: '', //   
    });
    const navigate = useNavigate();

    const handleChange = (e) => {
        const { name, value } = e.target;
        setFormData(prevState => ({
            ...prevState,
            [name]: value
        }));
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        //     
        if (parseInt(formData.age, 10) < 18) {
            alert('     18   .');
            return;
        }
        try {
            const response = await fetch('http://localhost:3360/api/auth/register', {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify(formData),
            });
            if (response.ok) {
                navigate('/login');
            } else {
                alert('  . ,   .');
            }
        } catch (error) {
            console.error(' :', error);
            alert('   -  .');
        }

        window.ym(97430458, 'reachGoal', 'Register');

    };

    return (
        <div className="register-container">
            <h2 className="register-title"></h2>
            <form onSubmit={handleSubmit} className="register-form">
                <input type="email" name="email" placeholder="Email" value={formData.email} onChange={handleChange} required />
                <input type="text" name="username" placeholder=" " value={formData.username} onChange={handleChange} required />
                <input type="password" name="password" placeholder="" value={formData.password} onChange={handleChange} required />
                <input type="text" name="city" placeholder="" value={formData.city} onChange={handleChange} required />
                <input type="text" name="region" placeholder="" value={formData.region} onChange={handleChange} required />
                <input type="tel" name="phoneNumber" placeholder=" " value={formData.phoneNumber} onChange={handleChange} required />
                <input type="number" name="age" placeholder="" value={formData.age} onChange={handleChange} required min="18" />
                <button type="submit"></button>
            </form>
        </div>
    );
};

export default RegisterPage;
