import React, { useState } from 'react';
import '../styles/AboutUsPage.css'; // Make sure you have this CSS file created
import teamImage from '../images/3635297.png'; // Adjust the path as necessary
import additionalImage from '../images/3740749.png'; // Path for the new image that will be shown after clicking 'Read More'

const AboutUsPage = () => {
    const [showMore, setShowMore] = useState(false);

    const toggleReadMore = () => {
        setShowMore(!showMore);
    };

    return (
        <div className="about-page">
            <div className="about-section">
                <div className="about-text">
                    <h1 className="about-title"> </h1>
                    <p className="about-description"> HealHub -  -,        ,   ,       .   HealHub          ,            ,
                           .   ,          ..</p>
                    <p className="about-description">  HealHub:

                         ,       ,    ..</p>
                    <button className="about-read-more" onClick={toggleReadMore}>
                        {showMore ? ' ' : ' '}
                    </button>
                </div>
                <div className="about-image">
                    <img src={teamImage} alt="Our Team" />
                </div>
            </div>
            {showMore && (
                <div className="additional-content">
                    <div className="additional-image">
                        <img src={additionalImage} alt="Additional Info" />
                    </div>
                    <div className="additional-text">
                        <h2>  HealHub  </h2>
                        <p>      .   HealHub          ..</p>
                    </div>
                </div>
            )}
        </div>
    );
};

export default AboutUsPage;
